/*--------------------------------------------------------------
Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:hover,
.screen-reader-text:active,
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar */
}

/*--------------------------------------------------------------
Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: rgb(44, 44, 44);
	font-family: 'Helvetica', Arial, sans-serif;
	font-size: 18px;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	margin: 0.3em;
}

p {
	margin-bottom: 1.5em;
	margin: 0.5em;
}

b,
strong {
	font-weight: bold;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	white-space: pre-wrap;
	font-size: 15px;
	font-size: 1.5rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font: 15px Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

sup,
sub {
	font-size: 75%;
	height: 0;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	bottom: 1ex;
}

sub {
	top: .5ex;
}

small {
	font-size: 75%;
}

big {
	font-size: 125%;
}

/* Components */

/*--------------------------------------------------------------
General styles
--------------------------------------------------------------*/


/* SideMenu
 **********************/
.sideMenu {
	display: none;
}

 a.list-group-item-action.list-group-item:hover,
 a.list-group-item-action.list-group-item:focus  {
	 background-color: #b9e0fa;
	 border-color: #52a4c7;
 }

 .sideMenu-category {
	 font-weight: 700;
 }

 /* Main
 **********************/
.mainHeader-style {
	background-color: #f5f9fa;
}
.mainHeader {
	font-size: 2.2rem;
	font-weight: 600;
	padding: 1em;
	margin-top: 0;
}

 /* Article
 **********************/
 .article {
	 padding: 1em;
	 margin-left: 1em;
 }
 .article-title {
	padding-bottom: 0.1em;
	 border-bottom: 2px solid #52a4c7;
 }
 .article-subtitle {
	font-size: 1.3rem;
 }
.article-parameters {
	font-size: 1rem;
	color: #3d7891;
}

 /* Title of Component
 **********************/





/*--------------------------------------------------------------
If no grid support, limit width to 50em and center align
--------------------------------------------------------------*/
@supports not (display: grid) {
	.site {
		max-width: 50em;
		margin: 0 auto;
	}
}

/*--------------------------------------------------------------
CSS Grid layout for wider screens, when browser supports grid:
--------------------------------------------------------------*/
@supports (grid-area: auto) {
	@media screen and (min-width: 50em) {
		.site {
			max-width: none;
			display: grid
		}

	/* App
	 **********************/
	.main-wrapper {
		display: grid;
		grid-template-columns: 15em 1fr;
	}
	/* Article
	 **********************/
	.article {
		margin-left: 1em;
	}

		/* SideMenu
	**********************/
	.sideMenu {
		display: block;
	}
	/* Bigger Screens */
}

	@media screen and (min-width: 65em) {
		.site {
			/*GRID*/
		}
	}
}

